@use '../util' as u;

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: u.$font;
  color: var(--text-color);
  background-color: var(--background-color)
}

button {
  cursor: pointer;
  appearance: none;
  border: none;
  outline: none;
  background: none;
}